.App {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: #008EFF;
  padding: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.Background {
  content: '';
  display: block;
  height: 100%;
  width: 100%;
  border-radius: 0;
  background: -webkit-radial-gradient(0% 0%, circle, rgba(0,255,231,.9),rgba(0,255,231,0) 50%), -webkit-radial-gradient(100% 100%, circle, rgba(0,255,231,.9),rgba(0,255,231,0) 60%);
  background: -o-radial-gradient(0% 0%, circle, rgba(0,255,231,.9),rgba(0,255,231,0) 50%), -o-radial-gradient(100% 100%, circle, rgba(0,255,231,.9),rgba(0,255,231,0) 60%);
  background: radial-gradient(circle at 0% 0%, rgba(0,255,231,.9),rgba(0,255,231,0) 50%), radial-gradient(circle at 100% 100%, rgba(0,255,231,.9),rgba(0,255,231,0) 60%);
}

.Container {
  content: '';
  display: block;
  height: 93%;
  width: 100%;
  border-radius: 0;
}

.Footer {
  opacity: 0.75;
  color: #404040;
  font-weight: bold;
  text-align: center;
  position: absolute;
  bottom: 5px;
  width: 100vw;
}



