
:local(.Outer) {
    composes: PageFrame from global;
}

:local(.PageContainer) {
    composes: Container from global;
    text-align: center;
    overflow: hidden;
}

.p {
    color: black;
    text-shadow: 1px 1px 0px white;
}

.failedMessage {
    color: red;
    font-style: italic;
    text-shadow: 1px 1px 0px white;    
}

.Heading {
    text-align: center;
    font-size: 2em;
}

.a {
    color: #008eff;
    text-decoration: none;
    font-weight: bold;
}

.a:visited {
    color: #008eff;
}

.Guestbook {
    overflow: auto;
    width: 100%;
    height: 100%;
    margin:auto;
}

.GuestbookEnterMessage {
    width: 49%;
    float: left;
    max-height: inherit;
    overflow: hidden;
    margin: auto;
    padding: 20px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.GuestbookReadMessages {
    width: 49%;
    float: right;
    overflow: hidden;
    margin: auto;
}


