::-webkit-scrollbar {
    width: 5px;
}
 
::-webkit-scrollbar-track {
    background-color: gainsboro;
}
 
::-webkit-scrollbar-thumb {
    background-color: #008eff;
}

.ComplexItemWrapper {
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    width: 100%;
    -ms-flex-align: center;
        align-items: center;
    /* display: flex */
}

.ItemImageField {
    width: 24px;
    margin-right: 10px;
    float: left;
}

.ItemDisplayField {
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    display: inline;
}

.ItemSecondaryField {
    float: right;
    font-weight: bold;
}


